import * as types from './mutation_type'
import { isArray } from 'util';
export default {
    [types.ICONDETAILLIST](state, data) {
        state.iconDetailList = data;
    },
    [types.SETCLONEICONDETAILLIST](state, data) {
        state.cloneIconDetailList = data;
    },
    [types.SETUSERNAME](state, data) {
        state.userInfo = data.data.data;
    },
    [types.SUSPENSIONENDTIOME](state, data) {
        state.SuspensionEndTime = data;
    },
    setChartData (state, data){
        state.chartData = data
    },
    setCurrentBuyInfo (state, data){
        if(isArray(data) || data.length === 0){
            state.currentBuyInfo = [...state.currentBuyInfo, ...data]
            return false
        }
        let obj = {
            name: 'XX标点',
            coord: [data.time, Number(data.price)],
            value: data.buyPrice,
            itemStyle: {
                normal: {color: '#ff9100'}
            }
        }
        obj.itemStyle.normal.color = data.buyStatus === 1 ? '#1dd086' : '#e62250'
        state.currentBuyInfo.push(obj)
    },
    setLastMinuteList (state, data){
        state.lastMinuteList = data
    },
    setHoldingList (state, data){
        state.holdingList = data
    },
    setSceenWidth (state, data){
        state.sceenWidth = data
    },
    setIconInfo (state, data){
        state.currentIconInfo = data
    },
    setAllIconNowPrice (state, data){
        state.allIconNowPrice = data
    }
}